.navbar {
 
    height: 60px;
    display: flex;
    justify-content: start;
    align-items: center;
    z-index: 1;
    
  }
  
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }
  
  .nav-menu {
    background: rgb(224,224,224);
    background: linear-gradient(0deg, rgba(224,224,224,1) 0%, rgba(198,198,198,1) 100%);
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 1;
    /* border-right: 2px solid black; */
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: black;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: grey;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background: none;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
    
  }
  
  span {
    margin-left: 16px;
  }